import { useEffect, useState } from "react";
import { setAccessToken, setMainAccessToken } from "./accessToken";
import App from "./App";
import Preloader from "./components/Preloader";
import {
    useGetMaintenanceQuery,
    useMaintenanceLoginMutation,
} from "./generated/graphql";
import URI from "./URI";

const AppWrapper = () => {
    const { data, loading } = useGetMaintenanceQuery();
    const [maintLogin] = useMaintenanceLoginMutation();

    const [pageLoader, setPageLoader] = useState(true);

    const [mainLoader, setMainLoader] = useState(true);
    useEffect(() => {
        //get new accessToken & refreshToken with fetch to URI/refresh_token
        try {
            let tmp = localStorage.getItem("iru5") as string;
            fetch(`${URI}/refresh_token`, {
                method: "POST",
                credentials: "include",
                headers: {
                    refreshToken: tmp,
                },
            }).then(async res => {
                const { accessToken, refreshToken } = await res.json();
                setAccessToken(accessToken);
                localStorage.setItem("iru5", refreshToken);
                setPageLoader(false);
            });
        } catch {
            setPageLoader(false);
        }

        try {
            // let tmp = localStorage.getItem("main_qwf") as string;
            // fetch(`${URI}/maintenance_login`, {
            //     method: "POST",
            //     credentials: "include",
            //     headers: {
            //         refreshToken: tmp,
            //     },
            // }).then(async res => {
            //     console.log("worked");
            //     const { accessToken, refreshToken } = await res.json();
            //     console.log("accessToken :>> ", accessToken);
            //     console.log("refreshToken :>> ", refreshToken);
            //     setMainAccessToken(accessToken);
            //     localStorage.setItem("main_qwf", refreshToken);
            //     console.log("setting");
            //     setMainLoader(false);
            // });
        } catch {}
    });

    if (pageLoader || loading) {
        return <Preloader />;
    } else if (mainLoader) {
        return (
            <>
                <button
                    onClick={async () => {
                        let acc = localStorage.getItem("access_id") as string;
                        let pass = localStorage.getItem("password") as string;
                        try {
                            const res = await maintLogin({
                                variables: {
                                    access_id: acc,
                                    password: pass,
                                },
                            });

                            if (res && res.data) {
                                let tmp = res.data.maintenanceLogin
                                    .accessToken as any;
                                setMainAccessToken(tmp!);
                                localStorage.setItem(
                                    "main_qwf",
                                    res.data.maintenanceLogin.refreshToken!
                                );
                                setMainLoader(false);

                                // window.location.reload();
                            }
                        } catch {}
                    }}
                >
                    App is in maintenance mode
                </button>
            </>
        );
    } else {
        return <App />;
    }
};

export default AppWrapper;
